import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a profile card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Profile(props) {
    const school = props.details.metaData.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span> {props.details.metaData.school}
            </p>
        ),
        department = props.details.metaData.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span> {props.details.metaData.department}
            </p>
        ),
        headshot = props.details.metaData.thumbnail && (
            <div className="card__thumbnail">
                <img
                    src={props.details.metaData.thumbnail}
                    alt={'Photo of ' + props.details.title}
                />
            </div>
        );

    return (
        <li className="card card--profile">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                {headshot}
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__type">
                        {props.details.metaData.jobtitle}
                    </p>
                    {school}
                    {department}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Profile.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Profile;
