import React from 'react';
import PropTypes from 'prop-types';
import Accommodation from '../../../finder/components/cards/finder__results__accommodation';
import CaseStudy from '../../../finder/components/cards/finder__results__casestudy';
import Centre from '../../../finder/components/cards/finder__results__centre';
import Contact from '../../../finder/components/cards/finder__results__contact';
import Course from '../../../finder/components/cards/finder__results__course';
import Event from '../../../finder/components/cards/finder__results__event';
import Funding from '../../../finder/components/cards/finder__results__funding';
import Generic from '../../../finder/components/cards/finder__results__generic';
import Module from '../../../finder/components/cards/finder__results__module';
import News from '../../../finder/components/cards/finder__results__news';
import Profile from '../../../finder/components/cards/finder__results__profile';

/**
 * Suggestions.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function InlineSearch__TopResults(props) {
    return (
        <div className="search--inline__topresults">
            <h3>Top results</h3>
            <ul>
                {props.results.map((result) => {
                    switch (result.collection) {
                        case 'web2020-residentialhalls':
                            return (
                                <Accommodation
                                    details={result}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-business-impact':
                            return (
                                <CaseStudy
                                    details={result}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-research':
                            return (
                                <CaseStudy
                                    details={result}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-centres':
                            return (
                                <Centre
                                    details={result}
                                    query={props.query}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-directory':
                            return (
                                <Contact
                                    details={result}
                                    query={props.query}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-courses':
                            return (
                                <Course
                                    details={result}
                                    query={props.query}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-events':
                            return (
                                <Event details={result} key={result.docNum} />
                            );
                        case 'web2020-funding':
                            return (
                                <Funding
                                    details={result}
                                    query={props.query}
                                    key={result.docNum}
                                />
                            );
                        case 'web2020-studyabroad':
                            return (
                                <Module details={result} query={props.query} />
                            );
                        case 'web2020-news':
                            return <News details={result} />;
                        case 'web2020-profiles':
                            return <Profile details={result} />;
                        default:
                            return <Generic details={result} />;
                    }
                })}
            </ul>
        </div>
    );
}

InlineSearch__TopResults.propTypes = {
    query: PropTypes.object,
    results: PropTypes.arrayOf(PropTypes.object),
};

export default InlineSearch__TopResults;
