import React from 'react';
import { render } from 'react-dom';
// import Search from './patterns/search/search';
import InlineSearch from './patterns/search/search--inline';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        // const search = document.querySelector('.wrapper--search'),
        const inlineSearch = document.querySelector('.global-header__search');

        // search &&
        //     searchConfig(search.dataset.config).then((config) => {
        //         render(<Search config={config} element={search} />, search);
        //     });

        inlineSearch &&
            finderConfig(inlineSearch.dataset.config).then((config) => {
                render(
                    <InlineSearch config={config} element={inlineSearch} />,
                    inlineSearch
                );
            });
    }
});
